
body {
	color: #333333;
	font-family: $font-Yugothic;
	font-size: 100%;
	-webkit-font-smoothing: antialiased;
	line-height: 1.4;
}


//header
#header{position: relative;
	.inner{
		display: flex;
		justify-content: space-between;
		align-items: center;
		.logo{
			@include font-size(30);
		}
		.nav-menu{
			@include mq(ipd){
				display: none;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				height: 100vh;
				padding: 60px 20px 0;
				overflow-y: scroll;
				&.active{
					display: block;
				}
			}
			.show-menu{
				display: flex;
				flex-wrap: wrap;
				align-items: flex-end;
				@include mq(ipd){
					display: block;
				}
				>.item{
					padding: 11px 17px;
					position: relative;
					@include mq(ipd){
						border-bottom: 1px solid #ccc;
					}
					>a{
						display: block;
						@include font-size(14);
						font-weight: bold;
						color: black;
						padding: 0 0;
						@include mq(ipd){
							text-align: center;
							position: relative;
						}
					}
				}
			}

		}
		.mobile-icon {
			width: 26px;
			height: 30px;
			-webkit-transition: background .5s;
			-o-transition: background .5s;
			transition: background .5s;
			position: absolute;
			right: 20px;
			top: 55%;
			-webkit-transform: translateY(-50%);
			-ms-transform: translateY(-50%);
			transform: translateY(-50%);
			cursor: pointer;
			z-index: 12;
			display: none;
			@include mq(ipd){
				display: block;
			}
			span{
				position: absolute;
				top: 50%;
				left: 50%;
				-webkit-transform: translateX(-50%);
				-ms-transform: translateX(-50%);
				transform: translateX(-50%);
				display: block;
				width: 100%;
				height: 2px;
				margin-top: -2px;
				background-color: #333;
				font-size: 0;
				-webkit-touch-callout: none;
				-webkit-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;
				user-select: none;
				-webkit-transition: background-color 0.3s;
				-o-transition: background-color 0.3s;
				transition: background-color 0.3s;
				border-radius: 3px;
				&:before,&:after{
					position: absolute;
					left: 0;
					width: 100%;
					height: 100%;
					background: #333;
					content: '';
					-webkit-transition: -webkit-transform 0.3s;
					transition: transform 0.3s;
					border-radius: 3px;
				}
				&:before{
					-webkit-transform: translateY(-250%);
					transform: translateY(-250%);
					top: -6px;
				}
				&:after{
					-webkit-transform: translateY(250%);
					transform: translateY(250%);
					bottom: -6px;
				}
			}
			&.mobile-close span {
				background-color: transparent;
				&:before,&:after{
					width: 100%;
					background: #333;
				}
				&:before {
					-webkit-transform: translateY(0) rotate(45deg);
					transform: translateY(0) rotate(45deg);
					top: 2px;
				}
				&:after {
					-webkit-transform: translateY(0) rotate(-45deg);
					transform: translateY(0) rotate(-45deg);
					bottom: -2px;
				}
			}
		}
	}
}


