body {
  color: #333333;
  font-family: "游ゴシック体", "Yu Gothic", YuGothic, "ヒラギノ角ゴPro W3", "Hiragino Kaku Gothic Pro", Osaka, "Meiryo", "メイリオ", "MS Pゴシック", Arial, Helvetica, Verdana, sans-serif;
  font-size: 100%;
  -webkit-font-smoothing: antialiased;
  line-height: 1.4;
}

#header {
  position: relative;
}

#header .inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header .inner .logo {
  font-size: 30px;
  font-size: 3rem;
}

@media screen and (max-width: 1024px) {
  #header .inner .nav-menu {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    padding: 60px 20px 0;
    overflow-y: scroll;
  }
  #header .inner .nav-menu.active {
    display: block;
  }
}

#header .inner .nav-menu .show-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}

@media screen and (max-width: 1024px) {
  #header .inner .nav-menu .show-menu {
    display: block;
  }
}

#header .inner .nav-menu .show-menu > .item {
  padding: 11px 17px;
  position: relative;
}

@media screen and (max-width: 1024px) {
  #header .inner .nav-menu .show-menu > .item {
    border-bottom: 1px solid #ccc;
  }
}

#header .inner .nav-menu .show-menu > .item > a {
  display: block;
  font-size: 14px;
  font-size: 1.4rem;
  font-weight: bold;
  color: black;
  padding: 0 0;
}

@media screen and (max-width: 1024px) {
  #header .inner .nav-menu .show-menu > .item > a {
    text-align: center;
    position: relative;
  }
}

#header .inner .mobile-icon {
  width: 26px;
  height: 30px;
  -webkit-transition: background .5s;
  -o-transition: background .5s;
  transition: background .5s;
  position: absolute;
  right: 20px;
  top: 55%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 12;
  display: none;
}

@media screen and (max-width: 1024px) {
  #header .inner .mobile-icon {
    display: block;
  }
}

#header .inner .mobile-icon span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  display: block;
  width: 100%;
  height: 2px;
  margin-top: -2px;
  background-color: #333;
  font-size: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
  border-radius: 3px;
}

#header .inner .mobile-icon span:before, #header .inner .mobile-icon span:after {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background: #333;
  content: '';
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  border-radius: 3px;
}

#header .inner .mobile-icon span:before {
  -webkit-transform: translateY(-250%);
  transform: translateY(-250%);
  top: -6px;
}

#header .inner .mobile-icon span:after {
  -webkit-transform: translateY(250%);
  transform: translateY(250%);
  bottom: -6px;
}

#header .inner .mobile-icon.mobile-close span {
  background-color: transparent;
}

#header .inner .mobile-icon.mobile-close span:before, #header .inner .mobile-icon.mobile-close span:after {
  width: 100%;
  background: #333;
}

#header .inner .mobile-icon.mobile-close span:before {
  -webkit-transform: translateY(0) rotate(45deg);
  transform: translateY(0) rotate(45deg);
  top: 2px;
}

#header .inner .mobile-icon.mobile-close span:after {
  -webkit-transform: translateY(0) rotate(-45deg);
  transform: translateY(0) rotate(-45deg);
  bottom: -2px;
}

